body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  padding: 10px 20px;
}

.comments-text {
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  margin: 0;
  padding: 10px 20px; }

.page_curtain_open {
  position: fixed;
  height: 100%; }

.news-comments-curtain {
  position: fixed;
  left: 0;
  right: 0;
  top: 10px;
  bottom: 0;
  transition: -webkit-transform 0.3s cubic-bezier(0.42, 0, 1, 1);
  transition: transform 0.3s cubic-bezier(0.42, 0, 1, 1);
  transition: transform 0.3s cubic-bezier(0.42, 0, 1, 1), -webkit-transform 0.3s cubic-bezier(0.42, 0, 1, 1);
  -webkit-transform: translateY(100%);
          transform: translateY(100%); }
  .news-comments-curtain_mode_folded {
    -webkit-transform: translateY(calc(100% - 52px));
            transform: translateY(calc(100% - 52px)); }
  .news-comments-curtain_mode_open {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  .news-comments-curtain-container {
    position: relative;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    background-color: #fff; }
    .news-comments-curtain_mode_open .news-comments-curtain-container {
      border-top-left-radius: 15px;
      border-top-right-radius: 15px; }
  .news-comments-curtain-overlay {
    position: absolute;
    left: 0;
    top: -100%;
    right: 0;
    bottom: 0;
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
    transition: background-color .3s, -webkit-transform 0s .3s;
    transition: background-color .3s, transform 0s .3s;
    transition: background-color .3s, transform 0s .3s, -webkit-transform 0s .3s; }
    .news-comments-curtain_mode_open .news-comments-curtain-overlay {
      background-color: rgba(0, 0, 0, 0.2);
      -webkit-transform: translateY(0);
              transform: translateY(0);
      transition: background-color .3s; }
  .news-comments-curtain-header {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 52px;
    -webkit-flex-shrink: 0;
            flex-shrink: 0; }
  .news-comments-curtain-fold {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 28px;
    -webkit-flex-shrink: 0;
            flex-shrink: 0; }
  .news-comments-curtain-content {
    overflow: auto;
    -webkit-overflow-scrolling: touch; }

